@import '../../styles/mixins';

.logo {
  display: flex;
  width: 64px;
  height: 22px;

  background-image: url(../../icons/nicegadgets-logo.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include onDesktop {
    width: 80px;
    height: 27px;
  }

  &--big {
    width: 89px;
    height: 30px;
  }
}